<template>
  <div class="container py-5">
    <!-- AdminNav Component -->
    <AdminNav />
    <Spinner v-if="isLoading" />
    <table 
      v-else
      class="table"
    >
      <thead class="thead-dark">
        <tr>
          <th scope="col">
            #
          </th>
          <th scope="col">
            Email
          </th>
          <th scope="col">
            Role
          </th>
          <th
            scope="col"
            width="140"
          >
            Action
          </th>
        </tr>
      </thead>
      <tbody>
        <tr 
          v-for="user in users"
          :key="user.id"
        >
          <th scope="row">
            {{user.id}}
          </th>
          <td>{{user.email}}</td>
          <td>{{user.isAdmin ? 'admin' : 'user'}}</td>
          <td>
            <button
            v-if="currentUser.id !== user.id"
            type="button"
            class="btn btn-link"
            @click.stop.prevent="toggleUserRole({userId: user.id, isAdmin: user.isAdmin})"
            >
            {{ user.isAdmin ? 'set as user' : 'set as admin' }}
            </button>
          </td>
        </tr>

      </tbody>
    </table>
  </div>
</template>

<script>
import adminAPI from '../apis/admin'
import AdminNav from './../components/AdminNav'
import { Toast } from './../utils/helpers'
import { mapState } from 'vuex'
import Spinner from './../components/Spinner'

export default {
  components: {
    AdminNav,
    Spinner
  },
  data () {
    return {
      users: [],
      isLoading: true
    }
  },
	computed: {
		...mapState(['currentUser'])
	},
  methods: {
    async fetchUser () {
			try {
				const { data, statusText } = await adminAPI.users.get()

				if (statusText !== 'OK') {
					throw new Error(statusText)
				} 

				this.users = data.users
        this.isLoading = false
			} catch (error) {
        this.isLoading = false
				console.log('error', error)
        Toast.fire({
          icon: 'error',
          title: '無法取得使用者資料，請稍後再試'
        })
			}
      
    },
    async toggleUserRole ({ userId, isAdmin }) {
			try {
        const willBeAdmin = !isAdmin
				const { data, statusText  } = await adminAPI.users.update({
          userId,
          isAdmin: willBeAdmin.toString()
          })
				
				if (data.status !== 'success' || statusText !== 'OK') {
					throw new Error(statusText)
				} 

				this.users = this.users.map(user => {
					if (user.id === userId) {
						return {
							...user,
							isAdmin: willBeAdmin
						} 
					}
					return user
				})
			} catch (error) {
				console.log('error', error)
        Toast.fire({
          icon: 'error',
          title: '無法更新會員角色，請稍後再試'
				})
			}
    }
  },
  created () {
    this.fetchUser ()
  }
  
}
</script>